/* Space out content a bit */
body {
  padding-top: 20px;
  padding-bottom: 20px; }

/* Everything but the jumbotron gets side spacing for mobile first views */
.header, .marketing, .footer {
  padding-right: 15px;
  padding-left: 15px; }

/* Custom page header */
.header {
  padding-bottom: 20px;
  border-bottom: 1px solid #e5e5e5; }

/* Make the masthead heading the same height as the navigation */
.header h3 {
  margin-top: 0;
  margin-bottom: 0;
  line-height: 40px; }

/* Custom page footer */
.footer {
  padding-top: 19px;
  color: #777;
  border-top: 1px solid #e5e5e5; }

/* Customize container */
@media (min-width: 768px) {
  .container {
    max-width: 730px; } }

.container-narrow > hr {
  margin: 30px 0; }

/* Main marketing message and sign up button */
.jumbotron {
  text-align: center;
  border-bottom: 1px solid #e5e5e5; }

.jumbotron .btn {
  padding: 14px 24px;
  font-size: 21px; }

/* Supporting marketing content */
.marketing {
  margin: 40px 0; }

.marketing p + h4 {
  margin-top: 28px; }

/* Space out the masthead */
.header {
  margin-bottom: 30px; }

/* Responsive: Portrait tablets and up */
@media screen and (min-width: 768px) {
  /* Remove the padding we set earlier */
  .header, .marketing, .footer {
    padding-right: 0;
    padding-left: 0; }
  /* Remove the bottom border on the jumbotron for visual effect */
  .jumbotron {
    border-bottom: 0; } }

.btn.text-muted {
  color: #999 !important; }

.modal-dialog {
  z-index: 1041; }

.panel .panel-body > h4 {
  margin-bottom: 20px;
  text-align: center; }
.panel .panel-body > p {
  color: #999;
  margin-bottom: 40px;
  text-align: center; }
.panel.panel-entry h4 {
  padding-bottom: 10px;
  margin-bottom: 10px; }
.panel.panel-entry ul {
  padding: 0;
  list-style: none; }
  .panel.panel-entry ul li {
    border-top: 1px solid #EEE;
    display: block;
    padding: 10px 0; }
    .panel.panel-entry ul li small {
      color: #999;
      float: right; }
    .panel.panel-entry ul li a {
      color: #333; }

